import * as THREE from 'three';
import ObjectArrayView from '../../generic/container/ObjectArrayView';
import Lathe from './Lathe';

export default function Torus(radius, tube, radialSegments, tubularSegments, phiStart, phiLength, thetaStart, thetaLength) {

  radialSegments = Math.floor(radialSegments) || 1;
  tubularSegments = Math.floor(tubularSegments) || 1;

  radius = radius || 100;
  tube = tube || 40;

  radialSegments = Math.max(radialSegments || 8, 3);
  tubularSegments = Math.max(tubularSegments || 6, 3);

  phiStart = THREE.Math.clamp(phiStart || 0, 0, Math.PI * 2);
  phiLength = THREE.Math.clamp(phiLength || Math.PI * 2, 0, Math.PI * 2 - phiStart);

  thetaStart = THREE.Math.clamp(thetaStart || 0, 0, Math.PI * 2);
  thetaLength = THREE.Math.clamp(thetaLength || Math.PI * 2, 0, Math.PI * 2 - thetaStart);

  const inverseTubularSegments = 1.0 / tubularSegments;
  const points = new ObjectArrayView(THREE.Vector3, tubularSegments + 1);
  const norms  = new ObjectArrayView(THREE.Vector3, tubularSegments + 1);
  const pt = new THREE.Vector3();
  const normPt = new THREE.Vector3();

  for (let i = 0, il = tubularSegments; i <= il; i++) {

    const theta = thetaStart + i * inverseTubularSegments * thetaLength;

    pt.set(
      radius + tube * Math.cos(theta),
      tube * Math.sin(theta),
      0
    );

    normPt.set(
      Math.cos(theta),
      Math.sin(theta),
      0
    );

    points.setAt(i, pt);
    norms.setAt(i, normPt.normalize());
  }

  return Lathe(points, norms, radialSegments, phiStart, phiLength);

};



