export default function BitSet(length) {

  this.data = new Uint32Array((length + 31) >>> 5);
  this.length = length;

  this.byteView = null;
};

BitSet.prototype = {

  constructor: BitSet,

  clear: function () {

    this.data.fill(0);
    return this;
  },

  fill: function (optionalOffset, optionalLength) {

    const data = this.data;

    let begin = optionalOffset !== undefined ? optionalOffset : 0;
    let until = optionalLength !== undefined ?
              begin + optionalLength : this.length;

    let wordIndexBegin = begin >>> 5;
    let wordIndexUntil = until >>> 5;

    // Smear the highbit with a signed shift for a pattern of MSBs:
    const maskBegin = -0x80000000 >> ~begin;

    // Power of two minus one yields a pattern of LSBs:
    const maskUntil = (1 << until) - 1;

    if (wordIndexBegin === wordIndexUntil)

      data[wordIndexBegin] |= (maskBegin & maskUntil);

    else {

      data[wordIndexBegin] |= maskBegin;

      if (++wordIndexBegin !== wordIndexUntil)
        data.fill(~0, wordIndexBegin, wordIndexUntil);

      data[wordIndexUntil] |= maskUntil;

    }

    return this;
  },

  include: function (i) {

    this.data[i >>> 5] |= 1 << i;
  },

  exclude: function (i) {

    this.data[i >>> 5] &= ~(1 << i);
  },

  contains: function (i) {

    return (this.data[i >>> 5] & 1 << i) !== 0;
  },

  swap: function (i, j) {

    const data = this.data;

    const wordIndexI = i >>> 5, maskI = 1 << i;
    const wordIndexJ = j >>> 5, maskJ = 1 << j;

    const stateI = (data[wordIndexI] & maskI) !== 0;
    const stateJ = (data[wordIndexJ] & maskJ) !== 0;

    if (stateI !== stateJ) {

      if (stateI) {

        data[wordIndexI] &= ~maskI;
        data[wordIndexJ] |= maskJ;

      } else {

        data[wordIndexI] |= maskI;
        data[wordIndexJ] &= ~maskJ;
      }
    }

  },

  testAndInclude: function (i) {

    const data = this.data;

    const wordIndex = i >>> 5, mask = 1 << i;

    const unchangedWord  = data[wordIndex];

    data[wordIndex] = unchangedWord | mask;
    return (unchangedWord & mask) !== 0;
  },

  includeFromArray: function (array, optionalOffset, optionalUntil) {

    const data = this.data;

    for (let j = optionalOffset || 0,
        e = optionalUntil || array.length; j < e; ++j) {

      const i = array[j];

      data[i >>> 5] |= 1 << i;
    }

    return this;
  },

  containsAnyIn: function (array, optionalOffset, optionalUntil) {

    let result = false;
    const data = this.data;

    for (let j = optionalOffset || 0,
        e = optionalUntil || array.length; j < e && !result; ++j) {

      const i = array[j];

      result = (data[i >>> 5] & 1 << i) !== 0;
    }

    return result;
  },

  countSetBits: function (optionalOffset, optionalUntil) {

    let result = 0;

    const begin = optionalOffset !== undefined ? optionalOffset : 0;
    const until = optionalUntil !== undefined ? optionalUntil : this.length;

    if ((until - begin & -0x4000) === 0)
      // that is, 'begin' is less than 'until' and fewer than 2048 bytes

      result = this.countSetBitsInShortRange(begin, until);

    else if (begin < until) {
      // use a LUT - it's several times faster at scale

      let data = this.byteView;
      const lut = BitSet._ByteBitCount;

      if (data === null) {

        data = new Uint8Array(this.data.buffer);
        this.byteView = data;
      }

      const wordIndexBegin = begin >>> 3;
      const wordIndexUntil = until >>> 3;

      // Smear the highbit with a signed shift for a pattern of MSBs:
      const maskBegin = -0x80000000 >> 31 - (begin & 7);
      // Power of two minus one yields a pattern of LSBs:
      const maskUntil = (1 << (until & 7)) - 1;

      result = lut[data[wordIndexBegin] & maskBegin];

      for (let j = wordIndexBegin + 1; j !== wordIndexUntil; ++j)
        result += lut[data[j]];

      result += lut[data[wordIndexUntil] & maskUntil];
    }

    return result;
  },

  countSetBitsInShortRange: function (begin, until) {

    // Note: For some odd reason the data word must be signed to keep
    // V8 from deoptimizing - all unsigned access won't do.

    let result = 0;

    const data = this.data;

    let here = begin;
    let index = here >>> 5;

    let word = data[index] >> here;

    result = word & 1;

    while (++here !== until) {

      if ((here & 31) !== 0)
        word >>= 1;
      else
        word = data[++index] | 0; // cast to signed (important)

      result += word & 1;
    }

    return result;
  },

  toIndexArray: function (optionalKnownLength) {

    const result = new Uint32Array(
          optionalKnownLength === undefined ?
              this.countSetBits() : optionalKnownLength);
    let baseIndex = 0;
    let writeOffset = 0;
    const data = this.data;

    for (let j = 0, n = data.length; j !== n; ++j) {

      for (let mask = data[j],
          i = baseIndex; mask !== 0; mask >>>= 1, ++i)

        if ((mask & 1) !== 0) result[writeOffset++] = i;

      baseIndex += 32;
    }

    return result;
  },

};

BitSet._ByteBitCount = (function () {

  const lut = new Uint8Array(256);

  for (let i = 0; i !== 256; ++i) {

    let value = 0;
    for (let m = i; m !== 0; m >>>= 1) if ((m & 1) !== 0) ++value;

    lut[i] = value;
  }

  return lut;

})();



