import * as THREE from 'three';
import ObjectArrayView from '../../generic/container/ObjectArrayView';
import Lathe from './Lathe';

export default function Cylinder(radiusTop, radiusBottom, height, radialSegments, heightSegments, openEnded, thetaStart, thetaLength) {

  radiusTop = radiusTop !== undefined ? radiusTop : 20;
  radiusBottom = radiusBottom !== undefined ? radiusBottom : 20;
  height = height !== undefined ? height : 100;

  openEnded = openEnded || false;

  radialSegments = Math.max(1, Math.floor(radialSegments || 8));
  heightSegments = Math.max(1, Math.floor(heightSegments || 8));

  thetaStart = THREE.Math.clamp(thetaStart || 0, 0, Math.PI * 2);
  thetaLength = THREE.Math.clamp(thetaLength || Math.PI * 2, 0, Math.PI * 2 - thetaStart);

  const heightSegmentLength = height / heightSegments;
  const halfHeight = height / 2;
  const slope = (radiusBottom - radiusTop) / height;

  const radialSegmentsLengthTop = radiusTop / radialSegments;
  const radialSegmentsLengthBottom = radiusBottom / radialSegments;

  let points;
  let norms;

  if (openEnded) {
    points = new ObjectArrayView(THREE.Vector3, heightSegments + 1);
    norms = new ObjectArrayView(THREE.Vector3, heightSegments + 1);
  } else {
    points = new ObjectArrayView(THREE.Vector3, heightSegments + 1 + (radialSegments + 1) * 2);
    norms = new ObjectArrayView(THREE.Vector3, heightSegments + 1 + (radialSegments + 1) * 2);
  }

  const pt = new THREE.Vector3();
  const normPt = new THREE.Vector3(0, -1, 0);

  if (!openEnded) {
    //top
    for (let i = 0, il = radialSegments; i <= il; i++) {

      pt.set(i * radialSegmentsLengthTop, -1 * halfHeight, 0);
      points.setAt(i, pt);
      norms.setAt(i, normPt);
    }
  }

  //body
  normPt.set(height, radiusTop - radiusBottom, 0);
  normPt.normalize();
  for (let i = 0, il = heightSegments; i <= il; i++) {

    pt.set(slope * i * heightSegmentLength + radiusTop, i * heightSegmentLength - halfHeight, 0);

    if (openEnded) {
      points.setAt(i, pt);
      norms.setAt(i, normPt);
    } else {
      points.setAt(i + radialSegments + 1, pt);
      norms.setAt(i + radialSegments + 1, normPt);
    }
  }

  if (!openEnded) {
    //bottom
    normPt.set(0, 1, 0);

    for (let i = 0, il = radialSegments; i <= il; i++) {

      pt.set(radiusBottom - i * radialSegmentsLengthBottom, 1 * halfHeight, 0);
      points.setAt(i + radialSegments + heightSegments + 2, pt);
      norms.setAt(i + radialSegments + heightSegments + 2, normPt);
    }
  }

  const latheSegments = radialSegments < 3 ? 3 : radialSegments;
  return Lathe(points, norms, latheSegments, thetaStart, thetaLength);
}



