import * as THREE from 'three';
import ObjectArrayView from '../../generic/container/ObjectArrayView';
import Lathe from './Lathe';

export default function Sphere(radius, widthSegments, heightSegments, phiStart, phiLength, thetaStart, thetaLength) {

  radius = radius || 50;

  widthSegments = Math.max(3, Math.floor(widthSegments || 8));
  heightSegments = Math.max(2, Math.floor(heightSegments || 6));

  phiStart = THREE.Math.clamp(phiStart || 0, 0, Math.PI * 2);
  phiLength = THREE.Math.clamp(phiLength || Math.PI * 2, 0, Math.PI * 2 - phiStart);

  thetaStart = THREE.Math.clamp(thetaStart || 0, 0, Math.PI);
  thetaLength = THREE.Math.clamp(thetaLength || Math.PI, 0, Math.PI - thetaStart);

  const inverseHeightSegments = 1.0 / heightSegments;
  const points = new ObjectArrayView(THREE.Vector3, heightSegments + 1);
  const norms  = new ObjectArrayView(THREE.Vector3, heightSegments + 1);
  const pt = new THREE.Vector3();

  for (let i = 0, il = heightSegments; i <= il; i++) {

    const theta = thetaStart + i * inverseHeightSegments * thetaLength;

    pt.set(
      radius * Math.sin(theta),
      -radius * Math.cos(theta),
      0
    );

    points.setAt(i, pt);
    norms.setAt(i, pt.normalize());
  }

  return Lathe(points, norms, widthSegments, phiStart, phiLength);

}



