export function glowFilter() {
  return h('svg', { attrs: { width: 0, height: 0 } }, [
    h('filter', { attrs: { id: 'glowfilter' } }, [
      h('feGaussianBlur', { attrs: { in: 'SourceAlpha', stdDeviation: 50, result: 'blur' } }),
      h('feOffset', { attrs: { in: 'blur', dx: 0, dy: 0, result: 'offsetBlur' } }),
      h('feFlood', { attrs: { 'flood-color': blurColor, 'flood-opacity': 1, result: 'offsetColor' } }),
      h('feComposite', { attrs: { in: 'offsetColor', in2: 'offsetBlur', operator: 'in', result: 'offsetBlurComposed' } }),
      h('feMerge', [
        h('feMergeNode', { attrs: { in: 'offsetBlurComposed' } }),
        h('feMergeNode', { attrs: { in: 'SourceGraphic' } }),
      ]),
    ]),
  ]);
}

