import * as THREE from 'three';

export default function getProjectionMatrix(cameraData) {
  const projectionMatrix = new THREE.Matrix4();
  if (cameraData.projection === 'Orthographic') {
    const size = /* this.computeCameraSize(true) ||*/ { width: 1, height: 1 };
    projectionMatrix.makeOrthographic(-size.width, size.width, -size.height,
        size.height, cameraData.nearClip, cameraData.farClip);
  } else {
    // const filmSizeVec = new THREE.Vector2(cameraData.get('filmSize'),
    //     cameraData.get('filmSize') * cameraData.get('aspectRatio'));
    //  console.log(navigator.getProjectionMatrix:, data.fieldOfView, data.aspectRatio);

    const ymax = cameraData.nearClip * Math.tan( THREE.Math.DEG2RAD * cameraData.fieldOfView * 0.5 );
    const ymin = - ymax;
    const xmin = ymin * cameraData.aspectRatio;
    const xmax = ymax * cameraData.aspectRatio;

    return projectionMatrix.makePerspective( xmin, xmax, ymax, ymin, cameraData.nearClip, cameraData.farClip );

  }

  return projectionMatrix;
}



