import { get, getAll, filter, find, set as sceneSet, setAll, clone as sceneClone } from './scene';
import { fetch as fetchScene } from './sceneIO';
import { openAnnotation as open, closeAnnotation as close } from './annotations';

//export availableActions = {
//  show: { args: [{key: 'name', type: 'String'}], description: 'Show nodes' },
//  hide: { args: [{key: 'name', type: 'String'}], description: 'Hide nodes' },
//};

export function show({ name }, { from }) {
  return (store) => {
    return setAll(store, { from, name, plug: 'Properties', property: 'visible' }, true);
  };
}

export function hide({ name }, { from }) {
  return (store) => {
    return setAll(store, { from, name, plug: 'Properties', property: 'visible' }, false);
  };
}

/**
 * setMaterial will assign a `materialName`, `to` a node. It will optionally `clone`
 * the material, and optionally fetch `fromSceneId`.
 *
 * @param {Object} attrs
 * @param {String} attrs.to
 * @param {String} attrs.materialName
 * @param {String} [attrs.fromSceneId]
 * @param {Boolean} [attrs.clone]
 * @param {Object} options
 * @param {String} [options.from] Optionally, a `from` base query to use to resolve the node
 */
export function setMaterial({ to, materialName, fromSceneId, clone }, { from }) {
  return (store) => {
    return (fromSceneId ? store.dispatch(fetchScene(fromSceneId)) : Promise.resolve(true)).then(function () {
      const matId = find(store, {
        from: (fromSceneId ? { id: fromSceneId } : null),
        name: materialName,
      });
      (clone && matId ? store.dispatch(sceneClone([matId])) : Promise.resolve({ [matId]: matId })).then(function (cloned) {
        var path = find(store, { from, name: to, plug: 'Material', property: 'reference' });
        return store.dispatch(setAll({ from, name: to, plug: 'Material', property: 'reference' }, cloned[matId]));
      });
    });
  };
}

/**
 * mate a `parentNode` to `targetNode`: Will align the transform of the `parentNode` to
 * that of `targetNode`. Set `translationOnly` to only set translation (must be previously aligned).
 *
 * @param {Object} attrs
 * @param {String} attrs.parentNode name of the node to move
 * @param {String} attrs.targetNode name of the target node
 * @param {Boolean} [attrs.translationOnly] change translation only, not complete transform.
 * @param {Object} options
 * @param {String} [options.from] Optionally, a `from` base query to use to resolve the parent node
 *
 */
export function mate({ parentNode, targetNode, translationOnly }, { from }) {
  return (store) => {
    var targetTranslation = get(store, { name: targetNode, evalPlug: 'Transform', property: 'translation' });
    var parentTranslation = get(store, { from, name: parentNode, evalPlug: 'Transform', property: 'translation' });

    if (targetTranslation && parentTranslation) {
      const diff = targetTranslation.clone().sub(parentTranslation);
      //console.log('diff?', parentNode, parentTranslation, 'to', targetNode, targetTranslation);
      //console.log('set', get(store, {id: from.id, property: 'name'}), diff);
      return store.dispatch(sceneSet({ id: from.id, plug: 'Transform', property: 'translation' }, diff));
    }

    return Promise.resolve(true);
  };
}

export function setImage(query, reference) {
  return (store) => {
    const ref = scene.find(reference);
    if (!ref) return Promise.resolve(true);
    return setAll(store, { name: query, plug: 'Material', property: 'baseMap' }, ref);
  };
}

export function setDefaultColor(query, value) {
  return (store) => {
    return setAll({ name: query, plug: 'Material', property: 'defaultColor' }, value);
  };
}

export function moveCamera(query, value) {
  return (store) => {
    const duration = Number(value) || 500;
    var camera = scene.find(query);
    if (camera) return animateCameraTo(camera, duration);
    return Promise.resolve(true);
  };
}

export function openAnnotation(query) {
  return (store) => {
    return Promise.all(filter(query).map((path) => open(store, path)));
  };
}

export function closeAnnotation(query) {
  return (store) => {
    return Promise.all(filter(query).map((path) => close(store, path)));
  };
}

export function set(query, value) {
  return (store) => {
    return setAll(store, query, value);
  };
}

const publicApi = {
  actions: { show, hide, mate, setMaterial, setImage, setDefaultColor, moveCamera,
    openAnnotation, closeAnnotation, set, },
  selectors: { },
};

export default publicApi;




